function dateOrdinalNumber(number, localeNumber) {
  if (number > 18 && number <= 31) {
    return localeNumber + "";
  } else {
    switch (number) {
      case 1:
        return localeNumber + "";
      case 2:
      case 3:
        return localeNumber + "";
      case 4:
        return localeNumber + "";
      default:
        return localeNumber + "";
    }
  }
}

const ordinalNumber = (dirtyNumber, options) => {
  const number = Number(dirtyNumber);
  const localeNumber = numberToLocale(number);
  const unit = options?.unit;

  if (unit === "date") {
    return dateOrdinalNumber(number, localeNumber);
  }
  if (number > 10 || number === 0) return localeNumber + "";

  const rem10 = number % 10;
  switch (rem10) {
    case 2:
    case 3:
      return localeNumber + "";
    case 4:
      return localeNumber + "";
    case 6:
      return localeNumber + "";
    default:
      return localeNumber + "";
  }
};

